/* header */
.row-header {
  height: 70px;
  font-size: 14px;
  flex-wrap: nowrap;
  justify-content: space-around; }

.col-menu {
  flex-grow: 0; }

.fa-equals {
  font-size: 32px; }

.col-logo {
  display: flex;
  align-items: center; }

.logo {
  width: 34px;
  height: 34px; }

.logo-text {
  font-family: 'Playfair Display', 'Georgia', serif;
  font-size: 25px;
  white-space: nowrap;
  margin-left: 15px; }

.search,
.translate {
  position: relative;
  font-family: 'Roboto', sans-serif;
  height: 32px; }
  .search input,
  .translate input {
    width: 100%;
    height: 100%;
    padding: 5px 12px;
    padding-right: 30px;
    color: #122d4b; }
  .search ::-webkit-search-cancel-button,
  .translate ::-webkit-search-cancel-button {
    position: relative;
    right: 5px; }
  .search button,
  .translate button {
    position: absolute;
    right: 5px;
    height: 100%; }
    .search button:focus,
    .translate button:focus {
      outline: none; }
  .search .fa-search,
  .translate .fa-search {
    color: #e4bd7d;
    font-size: 20px; }
  .search .search-icon,
  .translate .search-icon {
    margin-top: 3px; }

.col-translate {
  margin-right: auto; }

.translate {
  width: 100%; }
  .translate input {
    font-weight: bold;
    padding-left: 40px;
    padding-right: 30px; }
  .translate i.fa-exchange-alt {
    position: absolute;
    left: 10px;
    top: 8px;
    color: #1f476b; }
  .translate button {
    top: -1px;
    right: 5px;
    color: white;
    text-shadow: 0 2px #122d4b; }

.col-user button {
  width: 100%; }

.language {
  text-align: center; }
  .language .fa-chevron-down {
    position: relative;
    top: -2px;
    margin-left: 7px;
    color: #122d4b;
    text-shadow: 0 1px white; }

@media (max-width: 991px) {
  [class^=col] {
    padding-left: 10px;
    padding-right: 10px; }
  .logo-text {
    font-size: 16px; }
  .translate input {
    padding-left: 10px;
    padding-right: 20px; }
  i.fa-exchange-alt {
    display: none; } }

@media (max-width: 768px) {
  .logo-text {
    display: none; } }

@media (max-width: 575px) {
  .row-header {
    flex-wrap: wrap;
    height: auto;
    padding-top: 10px; }
  .col-search {
    order: 1;
    padding: 10px; } }

@media (max-width: 450px) {
  .col-language {
    display: none; } }

@media (max-width: 350px) {
  .col-translate {
    max-width: 35% !important; } }
